  }),
  methods: {
    checkInput (text) {
      const errors = []
      const pointDoc = document.getElementById('point1')
      if (text === 'confirm') {
        if (this.name === '') errors.push('„ÅäÂêçÂâç„ÅåÊú™ÂÖ•Âäõ„Åß„Åô„ÄÇ')
